// 初始化坐标轴
export function initAxis(wasmContext, id, title, axisStorke = "#228B22", axisAlignment = EAxisAlignment.Left, isy = true, lblcolor = "#4d4b4b") {
    return new NumericAxis(wasmContext, {
        // 坐标轴id
        id: id,
        // 坐标轴标题
        axisTitle: title,
        autoTicks: 5,
        maxAutoTicks: 10,
        minorsPerMajor: 5,
        // 坐标轴摆放位置
        axisAlignment: axisAlignment,
        // 设置坐标轴可见范围
        visibleRange: new NumberRange(0, 10),
        autoRange: EAutoRange.Once,
        isInnerAxis: false,
        drawMajorBands: false,
        axisBandsFill: "red",
        axisTitleStyle: {
            fontSize: isy == false ? 6 : 20,
            fontFamily: "Roboto",
            fontWeight: 600,
            color: axisStorke,
            padding:new Thickness(0,20,0,0)
            // alignment: ELabelAlignment.Right, //isy ==false ? ELabelAlignment.Left:ELabelAlignment.Right
        },
        axisBorder: {
            borderTop: isy == false ? 1 : 0,
            borderRight: isy ? 1 : 0,
            color: axisStorke, // Green color
        },
        majorGridLineStyle: {
            strokeThickness: 1,
            color: '#D4D4D4',
            strokeDashArray: [4, 2]
        },
        // 小网格线样式
        // minorGridLineStyle: {
        //     strokeThickness: 1,
        //     color: axisStorke,
        //     strokeDashArray: [2, 2]
        // },
        // 大刻度线样式
        majorTickLineStyle: {
            strokeThickness: 1,
            color: axisStorke,
            tickSize: 8,
            strokeDashArray: [10, 2]
        },
        // 小刻度线样式
        minorTickLineStyle: {
            strokeThickness: 1,
            color: axisStorke,
            tickSize: 4,
            strokeDashArray: [10, 2]
        },
        // 标签的样式
        labelStyle: {
            fontSize: 18,
            fontWeight: 600,
            color: lblcolor,
            // fontFamily: "Roboto",
        },
        // effect: new GlowEffect(wasmContext, {
        //     range: 0,
        //     intensity: 1,
        //     color: "#333333",
        //     offset: new Point(10, 10),
        // }),
    });
}

// 设置直线系列
export function initLineSerie(wasmContext, xid, yid, storke = "#228B22", strokeThickness = 2) {
    return new FastLineRenderableSeries(wasmContext, {
        xAxisId: xid,
        yAxisId: yid,
        strokeThickness: strokeThickness,
        stroke: storke,
        // pointMarker: new EllipsePointMarker(wasmContext, {  --显示点
        //     width: 6,
        //     height: 6,
        //     strokeThickness: 2,
        //     stroke: "SteelBlue",
        //     fill: "LightSteelBlue",
        // }),
    });
}

// 设置坐标轴数据精度
export function initNumericLabelPara(precision) {
    return {
        labelFormat: ENumericFormat.Decimal,
        labelPrecision: precision,
        useNativeText:true

        // cursorLabelFormat: ENumericFormat.Decimal,
        // cursorLabelPrecision: precision,
        // asyncLabels: true,
        // useSharedCache:false,
        // useNativeText:true
    };
}
// 添加十字光标修饰符
export function initCursorModifier(xid, yid, storke = "#3662EC", fillStorke = "#b36200") {
    return new CursorModifier({
        // 修饰符内部的注释要使用的xAxisId
        xAxisId: xid,
        // 修饰符内部的注释要使用的yAxisId
        yAxisId: yid,
        isEnabled: false,
        // 十字光标颜色
        crosshairStroke: storke,
        // 十字光标粗细
        crosshairStrokeThickness: 1,

        // 十字光标端点处的背景颜色
        axisLabelsFill: fillStorke,
        // 十字光标端点处的文字的颜色
        axisLabelsStroke: "#fff",
        // tooltipContainerBackground: "#000",
        // tooltipTextStroke: "#ff6600",
        // showTooltip: true,
        // strokeDashArray: [10, 2],
    });
}

export function initRolloverModifier(wasmContext, fillStorke = "#b36200") {
    let tooltipModifier = new RolloverModifier(wasmContext)
    tooltipModifier.axisLabelsFill = fillStorke;
    return tooltipModifier
}

export function addChartModifiers(chartModifiers) {
    chartModifiers.add(new MouseWheelZoomModifier());
    chartModifiers.add(new ZoomExtentsModifier());
    chartModifiers.add(new ZoomPanModifier());
    chartModifiers.add(new PinchZoomModifier());
    // chartModifiers.add(rubberBandZoomModifier);
    // chartModifiers.add(yAxisDragModifier);
    // chartModifiers.add(xAxisDragModifier);
}