import { XyDataSeries } from "scichart/Charting/Model/XyDataSeries";
import { NumericLabelProvider } from "scichart/Charting/Visuals/Axis/LabelProvider/NumericLabelProvider";
import { LeftAlignedOuterVerticallyStackedAxisLayoutStrategy } from "scichart/Charting/LayoutManager/LeftAlignedOuterVerticallyStackedAxisLayoutStrategy";
import { NumberRange } from "scichart/Core/NumberRange";
import { initAxis, initLineSerie, initNumericLabelPara, } from "./axisConfig";
import { XYChart } from "./chart";
import { EAxisAlignment } from "scichart/types/AxisAlignment";
// import { ECoordinateMode } from "scichart/Charting/Visuals/Annotations/AnnotationBase";
// import { TextAnnotation } from "scichart/Charting/Visuals/Annotations/TextAnnotation";
import { EHorizontalAnchorPoint, EVerticalAnchorPoint } from "scichart/types/AnchorPoint";
import { LineAnnotation } from "scichart/Charting/Visuals/Annotations/LineAnnotation";
import { BoxAnnotation } from "scichart/Charting/Visuals/Annotations/BoxAnnotation";
import { HorizontalLineAnnotation } from "scichart/Charting/Visuals/Annotations/HorizontalLineAnnotation";
import { CustomAnnotation } from "scichart/Charting/Visuals/Annotations/CustomAnnotation";
// import { VerticalLineAnnotation } from "scichart/Charting/Visuals/Annotations/VerticalLineAnnotation";
import { ELabelPlacement } from "scichart/types/LabelPlacement";
// 双y轴图表
export class XYYChart extends XYChart {
    constructor(xname, yname, y2name) {
        super(xname, yname)
        this.y2name = y2name;
    }
    async createChart(elementId = "scichart") {
        await super.createChart(elementId);
        let yid2 = "axis_y2";
        this.yAxis2 = initAxis(this.chart.wasmContext, yid2, this.y2name, "#00A248", EAxisAlignment.Left, true, "#00A248");
        this.yAxis2.labelProvider = new NumericLabelProvider(initNumericLabelPara(2));
        this.yAxis2.labelProvider.useCache = false;
        this.chart.sciChartSurface.yAxes.add(this.yAxis2);
        this.chart.sciChartSurface.layoutManager.leftOuterAxesLayoutStrategy = new LeftAlignedOuterVerticallyStackedAxisLayoutStrategy();
        this.xy2lineSerie = initLineSerie(this.chart.wasmContext, "axis_x", yid2, "#00A248");
        this.chart.sciChartSurface.renderableSeries.add(this.xy2lineSerie);
        this.xy2dataSeries = new XyDataSeries(this.chart.wasmContext, {
            containsNaN: false,
        });
        this.xy2lineSerie.dataSeries = this.xy2dataSeries;
        this.yRange2 = new NumberRange(0, 10);
    }

    setRectEO(arr) {
        this.box1.stroke = "green";
        this.box2.stroke = "green";

        // this.box3.textColor = "green";
        // this.box3.text = "hello world!";


        this.box4.stroke = "green";
        this.box5.stroke = "green";
        // this.box6.stroke = "green";

        this.box4.showLabel = true;
        this.box5.showLabel = true;
        // this.box6.showLabel = true;


        this.box1.x1 = arr[0];
        this.box1.y1 = arr[1];
        this.box1.x2 = arr[2];
        this.box1.y2 = arr[3];

        this.box2.x1 = arr[0];
        this.box2.y1 = arr[1];
        this.box2.x2 = arr[2];
        this.box2.y2 = arr[3];

        // this.box3.x1=arr[0];
        // this.box3.y1=arr[4];

        this.box4.y1 = arr[1];
        this.box5.y1=arr[4];
        // this.box6.x1=arr[0];
        this.box7.x1=arr[0];
        this.box7.y1=arr[1];
    }
    clearRectEO(arr) {
        this.box1.stroke = "transparent";
        this.box2.stroke = "transparent";

        // this.box3.x1=arr[0];
        // this.box3.y1=0;
        // this.box3.textColor = "red";
        // this.box3.text = "找不到落座扭矩点,请调整参数";
        this.box7.x1=arr[0];
        this.box7.x1=arr[0];


        this.box4.stroke = "transparent";
        this.box5.stroke = "transparent";
        // this.box6.stroke = "transparent";

        this.box4.showLabel = false;
        this.box5.showLabel = false;
        // this.box6.showLabel = false;
    }

    addRectEO(arr) {
        if (this.is_init == false)
            return;
        this.boxAnnotation = []
        //正方形
        this.box1 = new BoxAnnotation({
            xAxisId: "axis_x",
            yAxisId: "axis_y",
            fill: "transparent",
            stroke: "blue",
            strokeThickness: 3,
            x1: arr[0],
            y1: arr[1],
            x2: arr[2],
            y2: arr[3],
        });
        //斜线
        this.box2 = new LineAnnotation({
            xAxisId: "axis_x",
            yAxisId: "axis_y",
            fill: "transparent",
            stroke: "blue",
            strokeThickness: 3,
            x1: arr[0],
            y1: arr[1],
            x2: arr[2],
            y2: arr[3],
        });
        // 提示信息
        // this.box3 = new TextAnnotation({
        //     xAxisId: "axis_x",
        //     yAxisId: "axis_y",
        //     // fill: "transparent",
        //     textColor: "blue",
        //     fontSize:22,
        //     // strokeThickness: 3,
        //     x1: arr[0],
        //     y1: arr[1],
        //     text:"hello world!"
        // });
        //底下的水平线
        this.box4 = new HorizontalLineAnnotation({
            xAxisId: "axis_x",
            yAxisId: "axis_y",
            y1: arr[1],
            showLabel: true,
            stroke: "Red",
            strokeThickness: 2,
            axisLabelFill: "Red",
            axisFontSize: 10,
            labelPlacement: ELabelPlacement.TopLeft,
        });
        //上面的线
        this.box5 = new HorizontalLineAnnotation({
            xAxisId: "axis_x",
            yAxisId: "axis_y2",
            y1: arr[3],
            showLabel: true,
            stroke: "Red",
            strokeThickness: 2,
            axisLabelFill: "Red",
            axisFontSize: 10,
            labelPlacement: ELabelPlacement.TopLeft,
        });
        //竖线
        // this.box6 = new VerticalLineAnnotation({
        //     xAxisId: "axis_x",
        //     yAxisId: "axis_y2",
        //     x1: arr[0],
        //     // y1: 10.3,
        //     labelPlacement: ELabelPlacement.TopLeft,
        //     showLabel: true,
        //     stroke: "Red",
        //     strokeThickness: 2,
        //     axisLabelFill: "Red",
        //     axisFontSize: 10,

        // });
        //svg圆环
        this.box7 = new CustomAnnotation({
            xAxisId: "axis_x",
            yAxisId: "axis_y",
            x1: arr[0],
            y1: arr[3],
            verticalAnchorPoint: EVerticalAnchorPoint.Center,
            horizontalAnchorPoint: EHorizontalAnchorPoint.Center,
            svgString:
                '<svg id="Capa_1" xmlns="http://www.w3.org/2000/svg">' +
                '<g transform="translate(-50.867218,-10.091687)">' +
                '<circle cx="75" cy="31" r="20" stroke="yellow" stroke-width="2" fill="none" />' +
                "/>" +
                "</g>" +
                "</svg>"

        });
        this.boxAnnotation.push(this.box2,this.box4,this.box5,this.box7)
        this.chart.sciChartSurface.annotations.add(
            ...this.boxAnnotation
        )
        this.eo.active = true;

    }

    addSeriesData(xValues, yValues, yValues2) {
        super.addSeriesData(xValues, yValues)
        this.xy2dataSeries.appendRange(xValues, yValues2);
    }

    clearSeriesData() {
        if (this.is_init) {
            super.clearSeriesData();
            this.xy2dataSeries.clear();
        }
    }

    fit() {
        super.fit();
        this.yRange2 = this.yAxis2.getMaximumRange()
        this.yRange2.min = this.yRange2.min - this.yRange2.diff * 0.05;
        this.yRange2.max = this.yRange2.max + this.yRange2.diff * 0.05;
        this.yAxis2.visibleRange = this.yRange2;
    }

    fitWithEORange(min, max) {
        super.fit();
        this.yRange2 = this.yAxis2.getMaximumRange()
        this.yRange2.min = this.yRange2.min < min ? this.yRange2.min : min;
        this.yRange2.max = this.yRange2.max > max ? this.yRange2.max : max;
        this.yRange2.min = this.yRange2.min - this.yRange2.diff * 0.05;
        this.yRange2.max = this.yRange2.max + this.yRange2.diff * 0.05;
        this.yAxis2.visibleRange = this.yRange2;
    }

    setYRange2(min, max) {
        this.yRange2.min = min;
        this.yRange2.max = max;
        this.yAxis2.visibleRange = this.yRange2;
    }

    setY2Precision(precision) {
        this.yAxis2.labelProvider = new NumericLabelProvider(initNumericLabelPara(precision));
    }

    destory() {
        this.xy2dataSeries.delete();
        this.xy2lineSerie.delete();
        super.destory();
    }
}

export const CHART_TPT = new XYYChart("time", "angle", "torque")
window.addEventListener('beforeunload', () => {
    try {
        CHART_TPT.destory();
        console.log('=================== .close =====================>');
    } catch (err) {
        console.log(err);
    }
});