import { SciChartSurface } from "scichart/Charting/Visuals/SciChartSurface";
import { XyDataSeries } from "scichart/Charting/Model/XyDataSeries";
import { NumericLabelProvider } from "scichart/Charting/Visuals/Axis/LabelProvider/NumericLabelProvider";
import { EAxisAlignment } from "scichart/types/AxisAlignment";
import { NumberRange } from "scichart/Core/NumberRange"
// import { LegendModifier } from "scichart/Charting/ChartModifiers/LegendModifier";
import { theme } from "./chartTheme";

// ECoordinateMode
import {
    initAxis, initLineSerie,
    // initCursorModifier,
    initNumericLabelPara,
    addChartModifiers,
    reg_sci,
} from "./axisConfig";
// import { ECoordinateMode } from "scichart/Charting/Visuals/Annotations/AnnotationBase";
// import { TextAnnotation } from "scichart/Charting/Visuals/Annotations/TextAnnotation";
// import { EHorizontalAnchorPoint, EVerticalAnchorPoint } from "scichart/types/AnchorPoint";
// import { LineAnnotation } from "scichart/Charting/Visuals/Annotations/LineAnnotation";
import { BoxAnnotation } from "scichart/Charting/Visuals/Annotations/BoxAnnotation";
import { HorizontalLineAnnotation } from "scichart/Charting/Visuals/Annotations/HorizontalLineAnnotation";
import { VerticalLineAnnotation } from "scichart/Charting/Visuals/Annotations/VerticalLineAnnotation";
import { CustomAnnotation } from "scichart/Charting/Visuals/Annotations/CustomAnnotation";
import { EHorizontalAnchorPoint, EVerticalAnchorPoint } from "scichart/types/AnchorPoint";
import { ELabelPlacement } from "scichart/types/LabelPlacement";
class RectEO {
    constructor(xmin, xmax, ymin, ymax) {
        this.xmin = xmin;
        this.xmax = xmax;
        this.ymin = ymin;
        this.ymax = ymax;
        this.active = false;
        this.isOK = false;
        this.stroke = "transparent";
        this.strokeOK = "green";
        this.strokeNOK = "red";
    }
}

export class XYChart {
    constructor(xname, yname) {
        this.xname = xname;
        this.yname = yname;
        this.eo = new RectEO(0, 5, 0, 5);
    }
    async createChart(elementId) {
        reg_sci();
        // 获取上下文和画布
        this.chart = await SciChartSurface.createSingle(elementId, { theme });
        let xid = "axis_x";
        let yid = "axis_y";
        this.stepsAnnotation=[]
        // 在上下文,设置x轴名称及位置,
        this.xAxis = initAxis(this.chart.wasmContext, xid, "  ", "#3B3B3B", EAxisAlignment.Bottom, false);
        // this.xAxis = initAxis(wasmContext, xid, this.xname, "#3B3B3B", EAxisAlignment.Bottom, false);
        // 可以控制单个表的x,y轴中标间隔文本颜色
        this.yAxis = initAxis(this.chart.wasmContext, yid, this.yname, "#3B3B3B");
        // 向画布上添加x轴
        this.chart.sciChartSurface.xAxes.add(this.xAxis);
        // 向画布上添加y轴
        this.chart.sciChartSurface.yAxes.add(this.yAxis);
        // 设置x轴的数据精度
        this.xAxis.labelProvider = new NumericLabelProvider(initNumericLabelPara(2));
        this.yAxis.labelProvider = new NumericLabelProvider(initNumericLabelPara(2));
        this.xAxis.labelProvider.useCache = false;
        this.yAxis.labelProvider.useCache = false;
        // 在上下文里面添加数据系列(该数据系列可以作为其他线型的数据)
        this.dataSeries = new XyDataSeries(this.chart.wasmContext, {
            containsNaN: false,
            isSorted: false,
        });
        // 创建直线系列
        this.lineSerie = initLineSerie(
            this.chart.wasmContext,
            xid,
            yid,
            "#3B3B3B"
        );
        // 给直线绑定数据
        // 画布渲染直线
        this.chart.sciChartSurface.renderableSeries.add(this.lineSerie);
        this.lineSerie.dataSeries = this.dataSeries;
        //添加 鼠标缩放
        addChartModifiers(this.chart.sciChartSurface.chartModifiers);

        this.auto_fit = true
        if (this.hasEO) {
            this.addRectEO()
            this.xRange = new NumberRange(this.eo.xmin, this.eo.xmax);
            this.yRange = new NumberRange(this.eo.ymin, this.eo.ymax);
        }
        else {
            this.xRange = new NumberRange(0, 5);
            this.yRange = new NumberRange(0, 5);
        }
        this.is_init = true;
    }
    setRectEO(Angle, Force) {
        if (Angle == 0) {
            this.boxHorizontalLine.stroke = "transparent";
            this.boxVerticalLine.stroke = "transparent";
            this.boxHorizontalLine.axisLabelFill = "transparent";
            this.boxVerticalLine.axisLabelFill = "transparent";
        } else {
            this.boxHorizontalLine.stroke = "green";
            this.boxVerticalLine.stroke = "green";
            this.boxHorizontalLine.axisLabelFill = "red";
            this.boxVerticalLine.axisLabelFill = "red";
            this.boxHorizontalLine.y1 = Force;
            this.boxVerticalLine.x1 = Angle;
        }
    }

    addRectEO() {
        if (this.is_init == false)
            return;
        if (!this.boxAnnotation && this.chart.sciChartSurface.annotations.contains(this.boxAnnotation) == false) {
            this.boxAnnotation = new BoxAnnotation({
                xAxisId: "axis_x",
                yAxisId: "axis_y",
                fill: "transparent",
                stroke: "transparent",
                strokeThickness: 3,
                x1: this.eo.xmin,
                x2: this.eo.xmax,
                y1: this.eo.ymin,
                y2: this.eo.ymax
            });

            this.boxHorizontalLine = new HorizontalLineAnnotation({
                xAxisId: "axis_x",
                yAxisId: "axis_y",
                y1: 0,
                showLabel: true,
                stroke: "transparent",
                strokeThickness: 2,
                axisLabelFill: "transparent",
                axisFontSize: 23,
                labelPlacement: ELabelPlacement.Top,
            });

            this.boxVerticalLine = new VerticalLineAnnotation({
                xAxisId: "axis_x",
                yAxisId: "axis_y",
                x1: 0,
                // y1: 10.3,
                labelPlacement: ELabelPlacement.TopRight,
                showLabel: true,
                stroke: "transparent",
                strokeThickness: 2,
                axisLabelFill: "transparent",
                axisFontSize: 23,

            });


            this.chart.sciChartSurface.annotations.add(
                this.boxAnnotation, this.boxHorizontalLine, this.boxVerticalLine
            )
            this.eo.active = true;
        }
    }

    addStepsRectEO(steps) {
        if (steps.length === 0) return;
        this.stepsAnnotation = steps.map((step) => {
            return new CustomAnnotation({
                xAxisId: "axis_x",
                yAxisId: "axis_y",
                x1: step.angle,
                y1: step.torque,
                verticalAnchorPoint: EVerticalAnchorPoint.Bottom,
                horizontalAnchorPoint: EHorizontalAnchorPoint.Center,
                svgString:
                    '<svg id="Capa_1" xmlns="http://www.w3.org/2000/svg">' +
                    '<g transform="translate(-54.616083,-75.548914)">' +
                    '<path style="fill:#b22020;fill-opacity:0.34117648;stroke:#990000;stroke-width:1px;stroke-linecap:butt;stroke-linejoin:miter;stroke-opacity:1"' +
                    'd="m 55.47431,87.025547 c 7.158904,7.408333 7.158904,7.408333 7.158904,7.408333 L 69.79212,87.025547 H 66.212668 V 75.913042 h -7.158907 v 11.112505 z"' +
                    "/>" +
                    "</g>" +
                    "</svg>"

            });
        })
        this.chart.sciChartSurface.annotations.add(
            ...this.stepsAnnotation
        )
    }

    clearStepsRectEO() {
        if (this.stepsAnnotation.length === 0) return;
        this.stepsAnnotation.forEach(element => {
            this.chart.sciChartSurface.annotations.remove(
                element
            )
        });
    }

    resetEO() {
        if (this.boxAnnotation) {
            // this.boxAnnotation.stroke = this.eo.stroke
            this.boxAnnotation.x1 = this.eo.xmin;
            this.boxAnnotation.y1 = this.eo.ymin;
            this.boxAnnotation.x2 = this.eo.xmax;
            this.boxAnnotation.y2 = this.eo.ymax;
        }
    }

    setEoResult(rok, isOk) {
        if (this.is_init && this.boxAnnotation) {
            if (rok) {
                this.boxAnnotation.stroke = isOk == true ? this.eo.strokeOK : this.eo.strokeNOK;
            }
            else
                this.boxAnnotation.stroke = this.eo.stroke;
            // console.log(" this.boxAnnotation.stroke", this.boxAnnotation.stroke);
        }
    }

    // 给数据系列添加数据
    addSeriesData(xValues, yValues) {
        if (this.is_init)
            this.dataSeries.appendRange(xValues, yValues);
    }

    // 清空数据系列里面的数据
    clearSeriesData() {
        if (this.is_init)
            this.dataSeries.clear();
    }
    // 允许拖动轴进行缩放
    fit() {
        //没有曲线点时使用缩放会导致循环缩放并可能导致显示失真
        if (this.dataSeries.count() < 1 || this.is_init == false)
            return;
        this.xRange = this.xAxis.getMaximumRange()
        this.xRange.min = this.xRange.min - this.xRange.diff * 0.05;
        this.xRange.max = this.xRange.max + this.xRange.diff * 0.05;
        this.xAxis.visibleRange = this.xRange;
        this.yRange = this.yAxis.getMaximumRange()
        this.yRange.min = this.yRange.min - this.yRange.diff * 0.05;
        this.yRange.max = this.yRange.max + this.yRange.diff * 0.05;
        this.yAxis.visibleRange = this.yRange;
    }
    /**
     ** 带EO的自适应模式
     ** 未绘制曲线时,缩放到EO范围
     */
    fitWithEO() {
        if (this.dataSeries.count() < 1 || this.is_init == false) {
            return;
        }
        else {
            this.xRange = this.xAxis.getMaximumRange()
            if (this.eo.xmin < this.xRange.min)
                this.xRange.min = this.eo.xmin
            if (this.eo.xmax > this.xRange.max)
                this.xRange.max = this.eo.xmax
            this.xRange.min = this.xRange.min - this.xRange.diff * 0.05;
            this.xRange.max = this.xRange.max + this.xRange.diff * 0.05;
            this.xAxis.visibleRange = this.xRange;
            this.yRange = this.yAxis.getMaximumRange();
            if (this.eo.ymin < this.yRange.min)
                this.yRange.min = this.eo.ymin;
            if (this.eo.ymax > this.yRange.max)
                this.yRange.max = this.eo.ymax;
            this.yRange.min = this.yRange.min - this.yRange.diff * 0.05;
            this.yRange.max = this.yRange.max + this.yRange.diff * 0.05;
            this.yAxis.visibleRange = this.yRange;
        }
        // console.log(this.xRange, this.yRange);
    }
    /**
     ** 带EO的自适应模式,带扭矩的最值范围
     ** 未绘制曲线时,缩放到EO范围
     */
    fitWithEORange(min, max) {
        if (this.dataSeries.count() < 1 || this.is_init == false) {
            return;
        }
        else {
            this.xRange = this.xAxis.getMaximumRange()
            if (this.eo.xmin < this.xRange.min)
                this.xRange.min = this.eo.xmin
            if (this.eo.xmax > this.xRange.max)
                this.xRange.max = this.eo.xmax
            this.xRange.min = this.xRange.min - this.xRange.diff * 0.05;
            this.xRange.max = this.xRange.max + this.xRange.diff * 0.05;
            this.xAxis.visibleRange = this.xRange;
            this.yRange = this.yAxis.getMaximumRange();
            if (min > this.eo.ymin)
                min = this.eo.ymin
            if (max < this.eo.ymax)
                max = this.eo.ymax;
            if (min < this.yRange.min)
                this.yRange.min = min;
            if (max > this.yRange.max)
                this.yRange.max = max;
            this.yRange.min = this.yRange.min - this.yRange.diff * 0.05;
            this.yRange.max = this.yRange.max + this.yRange.diff * 0.05;
            this.yAxis.visibleRange = this.yRange;
        }
        // console.log(this.xRange, this.yRange);
    }

    setEoRange(xmin, xmax, ymin, ymax) {
        // if (xmin && xmax && ymin && ymax)
        this.eo.xmin = xmin;
        this.eo.xmax = xmax;
        this.eo.ymin = ymin;
        this.eo.ymax = ymax;
        this.resetEO()
    }

    // 设置数据范围
    setXRange(min, max) {
        this.xRange.min = min
        this.xRange.max = max
        this.xAxis.visibleRange = this.xRange;
    }

    setYRange(min, max) {
        this.yRange.min = min
        this.yRange.max = max
        this.yAxis.visibleRange = this.yRange;
    }

    //设置
    showCursor(show) {
        if (show) {
            //    cursorModifier.yAxisId = yid;
        }
        //cursorModifier.isEnabled = show;
    }
    // 设置精度
    setXPrecision(precision) {
        this.xAxis.labelProvider = new NumericLabelProvider(initNumericLabelPara(precision));
    }
    setYPrecision(precision) {
        this.yAxis.labelProvider = new NumericLabelProvider(initNumericLabelPara(precision));
    }
    destory() {
        if (this.dataSeries)
            this.dataSeries.delete();
        if (this.chart) {
            this.chart.sciChartSurface.delete();
            delete this.chart;
        }

    }

}

export const CHART_XY = new XYChart("angle", "torque");
export const CHART_TSPD = new XYChart("time", "speed");
// export const CHART_HIS_XY = new XYChart("angle", "torque");
// export const CHART_HIS_TSPD = new XYChart("time", "speed");
window.addEventListener('beforeunload', () => {
    try {
        CHART_XY.destory();
        CHART_TSPD.destory();
        console.log('=================== .close =====================>');
    } catch (err) {
        console.log(err);
    }
});
export default { CHART_XY, CHART_TSPD }

